-static .tooltip-text {\n  visibility: hidden;\n  background-color: black;\n  color: #fff;\n  text-align: center;\n  border-radius: 6px;\n  /* Position the tooltip */\n  position: absolute;\n  z-index: 1; }\n\n.tooltip-static:hover .tooltip-text {\n  visibility: visible;\n  padding: 5px; }\n\n/*styles for itt-tooltip directive*/\n.ittTooltip {\n  position: relative; }\n\n.ittTooltip__text {\n  background-color: black;\n  color: #fff;\n  text-align: center;\n  border-radius: 6px;\n  padding: 3px 5px;\n  /* Position the tooltip */\n  position: absolute;\n  /*transform: translate(0, 1.3em);*/\n  top: 1.13em;\n  left: 1.3em;\n  z-index: 1;\n  pointer-events: none; }\n\n.ittTooltip__text--url {\n  background-color: black;\n  color: #fff;\n  text-align: center;\n  border-radius: 6px;\n  padding: 3px 5px;\n  /* Position the tooltip */\n  position: absolute;\n  /*transform: translate(0, 1.3em);*/\n  top: 1.13em;\n  left: 1.3em;\n  z-index: 1;\n  pointer-events: none;\n  white-space: nowrap; }\n\n* {\n  box-sizing: border-box;\n  margin: 0;\n  padding: 0; }\n\nli {\n  list-style: disc outside none; }\n\nbody {\n  background-color: #FFF; }\n\n.clear:after {\n  clear: both;\n  content: \"\";\n  display: block; }\n\n.overlay {\n  pointer-events: none; }\n\n.overlay .item,\n.overlay.windowFg {\n  pointer-events: auto; }\n\n.videoMagnet {\n  /* No idea */\n  margin-bottom: -5px; }\n\n.videoMagnet img {\n  /* aspect ratio preserver */\n  width: 100%; }\n\n.small {\n  font-size: smaller; }\n\n.invisible {\n  visibility: hidden; }\n\na {\n  text-decoration: none;\n  color: #00C;\n  cursor: pointer;\n  cursor: hand; }\n\na:hover,\na:focus,\n.fakeLink:hover,\n.fakeLink:focus {\n  text-decoration: underline; }\n\na.button:hover,\na.button:focus {\n  text-decoration: none;\n  text-shadow: 0 0 5px #FFC; }\n\nh1,\nh2,\nh3,\nh4 {\n  margin: 0.8em 0 0.2em;\n  line-height: 1em; }\n\n/* --------------------------------------------------------------------------------\n loading indicator\n*/\n.loading {\n  text-align: center;\n  font-size: small;\n  min-height: 400px;\n  line-height: 0;\n  background-color: transparent; }\n\n.loading .spinner {\n  width: 60px;\n  height: 60px;\n  margin: 100px auto -30px auto;\n  position: relative;\n  background-color: #FFF;\n  opacity: 0.5; }\n\n.loading .pie {\n  width: 50%;\n  height: 100%;\n  position: absolute;\n  background-color: #DDD; }\n\n.loading .rotating {\n  border-radius: 60px 0 0 60px;\n  z-index: 2;\n  border-right: none;\n  animation: rotate 2s ease-in-out infinite;\n  transform-origin: 100% 50%; }\n\n.loading .filler {\n  border-radius: 0 60px 60px 0;\n  z-index: 1;\n  border-left: none;\n  animation: pieFill 2s steps(1, end) infinite;\n  left: 50%;\n  opacity: 0; }\n\n.loading .mask {\n  width: 50%;\n  height: 100%;\n  position: absolute;\n  z-index: 3;\n  opacity: 1;\n  background: inherit;\n  animation: pieMask 2s steps(1, end) infinite; }\n\n.loading__background {\n  position: fixed;\n  width: 100%;\n  height: 100%;\n  border: 2px solid black;\n  z-index: 99999;\n  opacity: 0.33;\n  background-color: #000; }\n\n.routeLoading {\n  position: fixed;\n  top: 30%;\n  right: 50%;\n  z-index: 99999;\n  min-height: 0;\n  width: 60px;\n  height: 60px;\n  border-radius: 3px;\n  box-shadow: 0px 0px 5px 3px rgba(0, 0, 0, 0.5); }\n\n.routeLoading .spinner {\n  margin: 0;\n  padding: 0; }\n\n.routeLoading .loading__text {\n  position: relative;\n  bottom: 30px; }\n\n@keyframes rotate {\n  0% {\n    transform: rotate(0deg); }\n  100% {\n    transform: rotate(360deg); } }\n\n@keyframes pieFill {\n  0% {\n    opacity: 0; }\n  50%,\n  100% {\n    opacity: 1; } }\n\n@keyframes pieMask {\n  0% {\n    opacity: 1; }\n  50%,\n  100% {\n    opacity: 0; } }\n\n/* --------------------------------------------------------------------------------\n position:fixed causes Safari to act less weird in fullscreen mode, but interferes with\n scrolling.  Disabling for now, let safari do what safari does.\n*/\n/*\n #CONTAINER {\n\tposi