: inline;\n\tmargin-left: 0;\n\twidth: auto;\n\ttext-align: left;\n\theight: 1px;\n\tpadding-right: 5px;\n}\n\n\n/* =========================== */\n\n.item.timestampSidebar .startTime {\n\ttransform: rotate(90deg) translateX(100%);\n\ttransform-origin: 100% 0;\n\twidth: auto;\n\tfont-size: 20px;\n\tmargin-left: -45px;\n}\n\n\n/* =========================== */\n\n.item.timestampSmall .displayTime {\n\tfont-size: 0.86em;\n}\n\n\n/* =========================== */\n\n\n/*============================ ============================ ============================\n *\n * Template variants\n *\n//=========================== */\n\n.item .pullquote:before {\n\tcolor: rgba(0, 0, 0, 0.1);\n\tcontent: \"“\";\n\tfloat: left;\n\tfont-size: 2.5em;\n\tline-height: 0.8em;\n\ttext-align: right;\n\twidth: 50px;\n\ttext-shadow: none !important;\n\theight: 1px;\n}\n\n.item .pullquote {\n\tfont-size: 2em;\n\tpadding-top: 0.3em;\n\tline-height: 1.1em;\n}\n\n.item .pullquoteSpeaker {\n\ttext-align: right;\n\tfont-style: italic;\n\tfont-size: 0.8em\n}\n\n/*now placing emdash in HTML*/\n/*.item .pullquoteSpeaker:before {*/\n\t/*content: \" — \";*/\n/*}*/\n\n\n/* - - - - - - - - - - - - - -  */\n\n.item .captionContainer {\n\tposition: relative;\n\toverflow: hidden;\n\twidth: 100%;\n}\n\n.item .caption {\n\tposition: absolute;\n\t/* TODO why is this not zero: */\n\tbottom: 4px;\n\tleft: 0;\n\tright: 0;\n\tpadding: 0.5em;\n\topacity: 1;\n\ttransition: 0.7s all;\n\tbackground-color: #333;\n\tbackground-color: rgba(0, 0, 0, 0.7);\n\tborder-top: 1px solid #666;\n\tcolor: #FFF;\n}\n\n.item .captionContainer img {\n\twidth: 100%;\n}\n\n.item.colorInvert .caption {\n\tbackground-color: #eee;\n\tbackground-color: rgba(255, 255, 255, 0.7);\n\tcolor: #000;\n\tborder-top: 1px solid #666;\n}\n\n.item:hover .caption {\n\topacity: 0;\n}\n\n.item .slidingcaption {\n\ttransform: translateY(0);\n}\n\n.item:hover .slidingcaption {\n\ttransform: translateY(100%);\n}\n\n\n/* - - - - - - - - - - - - - -  */\n\n.item .thumbnail img {\n\twidth: 100%;\n}\n\n.item .thumbnail {\n\tmax-width: 72px;\n\ttransition: all 0.7s;\n\tfloat: left;\n\tmargin-right: 1em;\n}\n\n.item .thumbnail.fullsize {\n\tmax-width: 100%;\n\tfloat: none;\n}\n\n\n/* - - - - - - - - - - - - - -  */\n\n.item .question label {\n\tdisplay: block;\n\tpadding: 0.3em;\n}\n\n.item .question .correct {\n\tborder-left: 5px solid #0C0;\n}\n\n.item .question .incorrect {\n\tborder-left: 5px solid #C00;\n}\n\n.item .question input[type=\"radio\"] {\n\tmargin-right: 0.5em;\n}\n\n.item .flotContainer {\n\tposition: relative;\n}\n\n.item .flotContainer:after {\n\tcontent: \"\";\n\tclear: both;\n\tdisplay: block;\n}\n\n.item .chartContainer {\n\twidth: 50%;\n\tmax-width: 400px;\n\tfloat: left;\n}\n\n.item .legendContainer {\n\tfloat: right;\n\twidth: 50%;\n}\n\n.item .legendContainer td {\n\tpadding: 2px;\n\tvertical-align: top;\n}\n\n.isNarrow .item .chartContainer {\n\twidth: auto;\n\tmax-width: auto;\n\tmargin: auto;\n\tfloat: none;\n}\n\n.isNarrow .item .legendContainer {\n\tfloat: none;\n\tmax-width: 100%;\n}\n\n\n/* - - - - - - - - - - - - - -  */\n\n.item .definition .itemHead div {\n\tpadding-left: 40px;\n\tbackground: url('/images/definition-1.png') top left no-repeat;\n}\n\n.item .definition .itemHead div span {\n\tfont-size: 1.6em;\n\tfont-weight: 700;\n\tline-height: 1.6em;\n\tborder-bottom: 1px solid;\n}\n\n.item .definition .text {\n\tpadding-left: 40px;\n\tbackground: url('/images/definition-2.png') top left no-repeat;\n}\n\n.item__text--transmedia,\n.item__text--h2,\n.item__text--image {\n  ul {\n    padding-left: 40px;\n  }\n}\n\n.item__text--pullquote ul {\n  padding-left: 80px;\n}\n\n.item__text--transcript,\n.item__text--link{\n  ul {\n    padding-left: 20px;\n  }\n}\n\n\n","/*\n\tExtra styling for 'current' items.\n\tFor now, avoid adding colors here, instead add images or rgba colors to modify existing background.\n\t(Once we have a solidified set of options in colors.css, may add matching variants for each of them here instead.)\n\n*/\n\n.reviewMode .item.isCurrent {\n\tbackground-color: rgba(0, 0, 0, 0.2);\n}\n.colorInvert .reviewMode .item.isCurrent {\n\tbackground-color: rgba(255, 255, 255, 0.3);\n}\n/* - - - - - - */\n/*disable transition for showCurrent scenes*/\n.content.allowHighlights .item {\n\ttransition: none;\n}\n\n.content.allowHighlights .highlightSolid.item.isCurrent {\n\tbackground-color: rgba(0, 0, 0, 0.2);\n}\n.content.allowHighlights .highlightBorder.item.isCurrent {\n\tborder: 3px solid rgba(0, 0, 0, 0.3);\n}\n.content.allowHighlights .highlightSide.item.isCurrent {\n\tborder-left: 10px solid rgba(0, 0, 0, 0.3);\n\ttransition: all ease-in-out 1s;\n\t/*box-shadow: -10px 0 0 rgba(0, 0, 0, 0.3);*/\n}\n.content.allowHighlights .highlightBloom.item.isCurrent {\n\tbackground-color: rgba(255, 255, 200, 0.4);\n\tbox-shadow: 0 0 8px rgba(255, 255, 200, 0.9);\n}\n.content.allowHighlights .highlightHighlighter.item.isCurrent {\n\tbackground-image: url('/images/highlighter.png');\n\tbackground-size: 100% 100%;\n}\n.content.allowHighlights .highlightTilt.item.isCurrent {\n\ttransform: rotateY(-20deg) scale(1.18);\n\ttransform-origin: 70% 50% 0;\n\ttransform-style: preserve-3d;\n}\n","/* \n\ttransitionFade\n\ttransitionSlideL\n\ttransitionSlideR\n\ttransitionPop\n*/\n/* Misc useful keyframes */\n\n@keyframes fadeOut {\n\t0% {\n\t\topacity: 1;\n\t}\n\t100% {\n\t\topacity: 0;\n\t}\n}\n\n@keyframes fadeIn {\n\t0% {\n\t\topacity: 0;\n\t}\n\t100% {\n\t\topacity: 1;\n\t}\n}\n\n@keyframes delayedFadeOut {\n\t0% {\n\t\topacity: 1;\n\t}\n\t33% {\n\t\topacity: 1;\n\t}\n\t100% {\n\t\topacity: 0;\n\t}\n}\n\n@keyframes delayedFadeIn {\n\t0% {\n\t\topacity: 0;\n\t}\n\t33% {\n\t\topacity: 0;\n\t}\n\t100% {\n\t\topacity: 1;\n\t}\n}\n\nbody {\n\t/* Addresses a small issue in webkit: http://bit.ly/NEdoDq */\n\t\n\t-webkit-backface-visibility: hidden;\n}\n/* ------------------------------------------------------------------------------------------------------------------ */\n/* \nDefaults.  Always include a bit of height animation, to make it look less jumpy when items are removed from\nthe middle of a list\n\nTransition time should be set for the specific object type\n */\n/* disabling the height animation for now; looks bad in Safari. TODO fix in safari */\n\n.animate.ng-enter,\n.animate.ng-leave,\n.animate.ng-move {\n\ttransition: all ease-in-out 0.5s;\n}\n\n.animate.noTransitions {\n\ttransition: none !important;\n}\n\n.content .animate.ng-enter {\n\t/*max-height: 1px;*/\n}\n\n.content .animate.ng-enter.ng-enter-active {\n\t/*max-height: 300px;*/\n}\n\n.content .animate.ng-leave {\n\t/*max-height:100px;*/\n\t\n\tdisplay: block !important;\n}\n\n.content .animate.ng-leave.ng-leave-active {\n\t/*max-height:0;*/\n}\n/* ------------------------------------------------------------------------------------------------------------------\n   transitionFade */\n\n.animate.ng-enter.transitionFade {\n\topacity: 0;\n}\n\n.animate.ng-enter-active.transitionFade {\n\topacity: 1;\n}\n\n.animate.ng-leave.transitionFade {\n\topacity: 1;\n}\n\n.animate.ng-leave.ng-leave-active.transitionFade {\n\topacity: 0;\n}\n/* ------------------------------------------------------------------------------------------------------------------\n   transitionSlideL */\n\n.animate.ng-enter.transitionSlideL {\n\ttransform: translateX(50px);\n\topacity: 0;\n}\n\n.animate.ng-enter-active.transitionSlideL {\n\ttransform: translateX(0);\n\topacity: 1;\n}\n\n.animate.ng-leave.transitionSlideL {\n\ttransform: translateX(0);\n\topacity: 1;\n}\n\n.animate.ng-leave.ng-leave-active.transitionSlideL {\n\ttransform: translateX(-50px);\n\topacity: 0;\n}\n/* ------------------------------------------------------------------------------------------------------------------\n   transitionSlideR */\n\n.animate.ng-enter.transitionSlideR {\n\ttransform: translateX(-50px);\n\topacity: 0;\n}\n\n.animate.ng-enter-active.transitionSlideR {\n\ttransform: translateX(0);\n\topacity: 1;\n}\n\n.animate.ng-leave.transitionSlideR {\n\ttransform: translateX(0);\n\topacity: 1;\n}\n\n.animate.ng-leave.ng-leave-active.transitionSlideR {\n\ttransform: translateX(50px);\n\topacity: 0;\n}\n/* ------------------------------------------------------------------------------------------------------------------\n   transitionExpandW */\n\n.animate.transitionExpandW {\n\toverflow: hidden;\n\tdisplay: block;\n}\n\n.animate.ng-enter.transitionExpandW {\n\tmax-width: 1px;\n\topacity: 0;\n}\n\n.animate.ng-enter-active.transitionExpandW {\n\tmax-width: 1200px;\n\topacity: 1;\n}\n\n.animate.ng-leave.transitionExpandW {\n\tmax-width: 1200px;\n\topacity: 1;\n}\n\n.animate.ng-leave.ng-leave-active.transitionExpandW {\n\tmax-width: 1px;\n\topacity: 0;\n}\n/* ------------------------------------------------------------------------------------------------------------------\n   transitionPop */\n\n.animate.ng-enter.transitionPop {\n\ttransform: scale(0.9);\n\topacity: 0;\n}\n\n.animate.ng-enter-active.transitionPop {\n\ttransform: scale(1);\n\topacity: 1;\n}\n\n.animate.ng-leave.transitionPop {\n\ttransform: scale(1);\n\topacity: 1;\n}\n\n.animate.ng-leave.ng-leave-active.transitionPop {\n\ttransform: scale(0.9);\n\topacity: 0;\n}\n/* ------------------------------------------------------------------------------------------------------------------\n   transitionNone */\n\n.animate.ng-enter.transitionNone,\n.animate.ng-leave.transitionNone,\n.animate.ng-move.transitionNone {\n\ttransition: all ease-in-out 0s;\n}\n\n","/*============================\n *\n * Default\n *\n//===========================*/\n\nbody {\n\tfont-family: 'Open Sans', sans-serif\n}\n\na {\n\ttext-decoration: none;\n}\n\n\n/* TODO see what these were in pre-4.0 */\n\n.title {\n\tfont-size: 1.2em;\n\tline-height: 1em;\n}\n\n.authorName,\n.category {\n\tfont-size: 0.8em;\n\tfont-weight: 700\n}\n\n\n/*============================\n *\n * typographySans\n *\n//===========================*/\n\n.typographySans {\n\tfont-family: 'Open Sans', sans-serif\n}\n\n.item.typographySans,\n.item.typographySans h1,\n.item.typographySans h2,\n.item.typographySans h3,\n.item.typographySans a {\n\tfont-family: 'Open Sans', sans-serif !important;\n}\n\n\n/*============================\n *\n * typographySerif\n *\n//===========================*/\n\n.typographySerif {\n\tfont-family: 'Droid Serif', serif\n}\n\n.item.typographySerif,\n.item.typographySerif h1,\n.item.typographySerif h2,\n.item.typographySerif h3,\n.item.typographySerif a {\n\tfont-family: 'Droid Serif', serif !important;\n}\n\n\n/*============================\n *\n * typographyBook\n *\n//===========================*/\n\n.typographyBook {\n\tfont-family: 'EB Garamond', 'Adobe Garamond', Garamond, serif;\n}\n\n.item.typographyBook h1,\n.item.typographyBook h2,\n.item.typographyBook h3 {\n\tfont-family: 'Trajan Pro', 'Trajan', 'EB Garamond', 'Adobe Garamond', Garamond, serif !important;\n}\n\n\n/*============================\n *\n * typographySwiss\n *\n//===========================*/\n\n.typographySwiss {\n\tfont-family: 'Roboto', sans-serif;\n}\n\n.item.typographySwiss,\n.item.typographySwiss h1,\n.item.typographySwiss h2,\n.item.typographySwiss h3,\n.item.typographySwiss a {\n\tfont-family: 'Roboto', sans-serif !important;\n}\n\n.typographySwiss .authorName,\n.typographySwiss .title {\n\tfont-weight: 300\n}\n\n\n/*============================\n *\n * customer specific:   typographyEliterate, typographyGw, typographyUsc, typographyPurdue\n *\n//===========================*/\n\n.typographyEliterate {\n\tfont-family: Arial, Helvetica, Tahoma, sans-serif;\n\tfont-size: 15px;\n}\n\n\n/* ----- */\n\n.typographyGw {\n\tfont-family: \"Helvetica\", \"Arial\", sans-serif;\n\tfont-weight: 300;\n}\n\n.typographyGw h1,\n.typographyGw h2,\n.typographyGw h3,\n.typographyGw .item a,\n.typographyGw.navPanel a {\n\tfont-family: \"Montserrat\", sans-serif;\n}\n\n.typographyGw h1,\n.typographyGw h2,\n.typographyGw h3 {\n\tfont-weight: 700\n}\n\n.typographyGw